import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { TransactionRoutingModule } from './transaction-routing.module';
import { CheckoutComponent } from './checkout/checkout.component';
import { CheckinComponent } from './checkin/checkin.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { CheckinAddComponent } from './checkin-add/checkin-add.component';


@NgModule({
    declarations: [CheckoutComponent, CheckinComponent, CheckinAddComponent],
    imports: [
        CommonModule,
        TransactionRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
    ],
    providers: [FormBuilderTypeSafe],
})
export class TransactionModule { }
