import { Component, OnInit } from '@angular/core';
import { AssetModelxService, VendorVendorService, AssetPartIdService, AssetStatusService, AssetAssetService, AssetBinLocationService, AssetModelOwnerService } from 'src/app/sdk/asset/services';
import { Modelx, Vendor, Status, Part_id, Asset, Model_owner, Bin_location } from 'src/app/sdk/asset/models';
import { Router, ActivatedRoute } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { NgxSpinnerService } from 'ngx-spinner';
import { Validators, FormControl } from '@angular/forms';
import { Kantor } from 'src/app/sdk/hr/models';
import { CompanyKantorService } from 'src/app/sdk/hr/services';

@Component({
    selector: 'app-checkin-add',
    templateUrl: './checkin-add.component.html',
    styleUrls: ['./checkin-add.component.scss']
})
export class CheckinAddComponent implements OnInit {

    constructor(
        private assetAssetService: AssetAssetService,
        private assetModelxService: AssetModelxService,
        private vendorVendorService: VendorVendorService,
        private assetStatusService: AssetStatusService,
        private assetPartIdService: AssetPartIdService,
        private assetBinLocationService: AssetBinLocationService,
        private assetModelOwnerService: AssetModelOwnerService,
        private companyKantorService: CompanyKantorService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activeRouter: ActivatedRoute,
    ) { }

    model: Modelx = {}

    listVendor: Vendor[] = []
    listStatus: Status[] = []
    listOwner: Model_owner[] = []
    listKantor: Kantor[] = []
    listBin: Bin_location[] = []


    id_model: string = ''
    ngOnInit() {
        this.activeRouter.paramMap.subscribe(
            data => {
                if (data.get('id_model')) {
                    this.id_model = data.get('id_model')
                }
            }
        )

        this.getPreeData()
        this.form()
    }

    getPreeData() {
        this.spinner.show()
        let status = 0;
        this.assetModelxService.getAssetModelxesId(this.id_model).subscribe(
            data => {
                this.model = data.data
                this.getPartId(this.model.id_part_id)
                this.spinner.hide()
            },
            err => {
                this.spinner.hide()
            }
        )
        this.vendorVendorService.getVendorVendors().subscribe(data => {
            this.listVendor = data.data
        })
        this.assetStatusService.getAssetStatuses(JSON.stringify({ id_status_type: 'serviceable' })).subscribe(data => {
            this.listStatus = data.data
        })
        this.assetModelOwnerService.getAssetModelOwners(JSON.stringify({id_model: this.id_model})).subscribe(data => {
            this.listOwner = data.data
        })
        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })
        this.assetBinLocationService.getAssetBinLocations().subscribe(data => {
            this.listBin = data.data
        })

    }


    partId: Part_id = {}
    getPartId(id_part_id) {
        this.assetPartIdService.getAssetPartIdsId(id_part_id).subscribe(data => {
            this.partId = data.data
        })
    }

    dataForm: FormGroupTypeSafe<Asset>
    form() {

        let sn = [];

        this.dataForm = this.fb.group<Asset>({
            id_model: new FormControl(this.id_model),
            sn: new FormControl('', sn),
            id_status: new FormControl('', [Validators.required]),
            id_vendor: new FormControl('', [Validators.required]),
            ref_input: new FormControl('app'),
            tag: new FormControl(''),
            for_module_position: new FormControl('kantor'),
            for_id_module_position: new FormControl('0'),
            purchase_number: new FormControl(''),
            purchase_date: new FormControl(''),
            manufacture_date: new FormControl(''),
            price: new FormControl(''),
            id_bin_location: new FormControl(''),
            id_kantor: new FormControl(''),
            purchase_condition: new FormControl(''),
            id_owner: new FormControl(''),
            qty: new FormControl(1, [Validators.required]),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }
        console.log()
        if (this.model.is_single == 'y') {
            if (this.dataForm.get(['sn']).value == null || this.dataForm.get(['sn']).value == "") {
                this.pesan.pesanWarningForm('Serial number tidak boleh kosong')
                this.spinner.hide()
                return false
            }
            if (this.dataForm.get(['qty']).value > 1) {
                this.pesan.pesanWarningForm('QTY tidak boleh lebih dari 1')
                this.spinner.hide()
                return false
            }

        }


        this.assetAssetService.postAssetAssets(this.dataForm.value).subscribe(
            data => {
                this.goDetailPart(data.data.id_asset)
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/asset/master/model/list'])
    }

    goDetailPart(id_part) {
        this.router.navigate(['/asset/detail', id_part])
    }

}
